import store from '../store'
import { handleLoginFailure } from '@/utils'
import { VUE_APP_API_URL } from '@/config'
import cookie from '@/utils/store/cookie'

const fly = new Fly()
fly.config.baseURL = VUE_APP_API_URL

// 

// fly.config.baseURL = 'http://29e25012l6.qicp.vip/api'







fly.interceptors.response.use(
  response => {
    // console.log(response)
    // access-token
    return response
  },
  error => {
	console.log(error)
    if (error.toString() == 'Error: Network Error') {
      console.log('')
      console.log('', error)
      console.log('')
      handleLoginFailure()
      return Promise.reject({ msg: '', toLogin: true })
    }
    if (error.status == 401) {
      console.log('')
      console.log(' 401', error)
      console.log('')
      handleLoginFailure()
      return Promise.reject({ msg: '', toLogin: true })
    }
    if (error.response.data.status == 5109) {
      uni.showToast({
        title: error.response.data.msg,
        icon: 'none',
        duration: 2000,
      })
    }
    return Promise.reject(error)
  }
)

const defaultOpt = { login: true }

function baseRequest(options) {
  //  token  token 
  const token = cookie.get('login_status')

  //  headers
  //  token 
console.log(options)

  options.headers = {
    ...options.headers,
	// hahah: 'Bearer2 ',
  }
  // if(options.method=='post'){
	//   options.headers.hahah='Bearer2 '
  // }

  if (options.login === true) {
    options.headers = {
      ...options.headers,
      Authorization: 'Bearer ' + token,
    }
  }

  // //  token 
  // if (options.login === true && !token) {
  //   // 
  //   handleLoginFailure()
  //   // 
  //   return Promise.reject({ msg: '', toLogin: true })
  // }
  // 
  const { url, params, data, login, ...option } = options

  // 
  return fly
    .request(url, params || data, {
      ...option,
    })
    .then(res => {
      const data = res.data || {}
      if (res.status !== 200) {
        return Promise.reject({ msg: '', res, data })
      }
      console.log(data)
      if ([401, 403].indexOf(data.status) !== -1) {
        handleLoginFailure()
        return Promise.reject({ msg: res.data.msg, res, data, toLogin: true })
      } else if (data.status === 200) {
        return Promise.resolve(data, res)
      } else if (data.status == 5101) {
        return Promise.reject({ msg: res.data.msg, res, data })
      } else {
        return Promise.reject({ msg: res.data.msg, res, data })
      }
    })
}

/**
 * http 
 *  https://www.kancloud.cn/yunye/axios/234845
 *
 */
const request = ['post', 'put', 'patch'].reduce((request, method) => {
  /**
   *
   * @param url string 
   * @param data object get
   * @param options object axios 
   * @returns {AxiosPromise}
   */
  request[method] = (url, data = {}, options = {}) => {
    return baseRequest(Object.assign({ url, data, method }, defaultOpt, options))
  }
  return request
}, {});

['get', 'delete', 'head'].forEach(method => {
  /**
   *
   * @param url string 
   * @param params object get
   * @param options object axios 
   * @returns {AxiosPromise}
   */
  request[method] = (url, params = {}, options = {}) => {
    return baseRequest(Object.assign({ url, params, method }, defaultOpt, options))
  }
})

export default request
