import {
	cancelOrder,
	takeOrder,
	delOrder,
	payOrder,
	getSubscribeTemplate
} from "@/api/order";
import dialog from "@/utils/dialog";
import {
	weappPay
} from "@/libs/wechat";

import {
	_router
} from '@/utils'

export function cancelOrderHandle(orderId) {
	return new Promise((resolve, reject) => {
		uni.showModal({
			title: '',
			content: '?',
			success(res) {
				if (res.confirm) {
					cancelOrder(orderId)
						.then(res => {
							uni.showToast({
								title: '',
								icon: 'success',
								duration: 2000
							});
							resolve(res);
						})
						.catch(err => {
							uni.showToast({
								title: '',
								icon: 'none',
								duration: 2000
							});
							reject(err);
						});
				} else if (res.cancel) {}
			}
		})
	});
}

export function takeOrderHandle(orderId) {
	return new Promise((resolve, reject) => {
		takeOrder(orderId)
			.then(res => {
				uni.showToast({
					title: '',
					icon: 'success',
					duration: 2000
				});
				resolve(res);
			})
			.catch(err => {
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 2000
				});
				reject(err);
			});
	});
}

export function delOrderHandle(orderId) {
	return new Promise((resolve, reject) => {
		dialog.confirm({
			mes: "?",
			opts() {
				delOrder(orderId)
					.then(res => {
						uni.showToast({
							title: '',
							icon: 'success',
							duration: 2000
						});
						resolve(res);
					})
					.catch(err => {
						uni.showToast({
							title: '',
							icon: 'none',
							duration: 2000
						});
						reject(err);
					});
			}
		});
	});
}

// 
export async function payOrderHandle(orderId, type, from) {
	return new Promise((resolve, reject) => {
		uni.showLoading({
			title: "",
			mask: true
		});
		payOrder(orderId, type, from)
			.then(async res => {
				console.log(res)
				await handleOrderPayResults(res.data, type)
				resolve()
			})
			.catch(err => {
				reject()
				uni.hideLoading()
				uni.showToast({
					title: err.msg || err.response.data.msg || err.response.data.message || '',
					icon: "none",
					duration: 2000,
				});
			});
	});
}

// 
// @type create()||pay()
export function handleOrderPayResults(data, type, payType) {

	//subscribeMessage()

	return new Promise((resolve, reject) => {
		uni.hideLoading()

		switch (data.status) {
			// 
			case "ORDER_EXIST":
				resolve()
				break;
				// 
			case "EXTEND_ORDER":
				uni.showToast({
					title: data.msg,
					icon: "none",
					duration: 2000,
				});
				resolve()
				goOrderDetails(data.result.orderId, type)
				break;
			case "PAY_DEFICIENCY":
				break;
				// 
			case "PAY_ERROR":
				uni.showToast({
					title: data.msg,
					icon: "none",
					duration: 2000,
				});
				reject()
				goOrderDetails(data.result.orderId, type)
				break;
				// 
			case "SUCCESS":
				uni.showToast({
					title: data.msg || data.payMsg,
					icon: "none",
					duration: 2000,
				});
				resolve()
				goOrderDetails(data.result.orderId, type)
				break;
				// H5
			case "WECHAT_H5_PAY":
				goOrderDetails(data.result.orderId, type)
				console.log(data)
				setTimeout(() => {
					resolve()




				}, 100);
				break;
				// 
			case "WECHAT_PAY":
				weappPay(data.result.jsConfig).finally(() => {
					resolve()
					goOrderDetails(data.result.orderId, type)
				}).then(res => {

					//subscribeMessage()

				})
				break;
				// APP
			case "WECHAT_APP_PAY":
				weappPay(data.result.jsConfig).finally(() => {
					resolve()
					goOrderDetails(data.result.orderId, type)
				})
				break;
		}
	})
}

